body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.users {
  display: inline-block;
  margin-bottom: 20px;
}

.users .user {
  display: -webkit-flex;
  display: flex;
  margin: 10px 0;
}

.users h2 {
  text-transform: uppercase;
  color: #333;
}

.users div img {
  height: 70px;
  width: 70px;
  margin-right: 5px;
  box-shadow: 0 0 0 1px black;
}

.users div p {
  margin-right: 5px;
  font-size: 20px;
  font-weight: bold;
}

.users div p.id {
  font-weight: 400;
  margin-right: 15px;
}

.users p.message {
  font-size: 20px;
  color: #e50914;
}

.users button.delete {
  background-color: #e62d36;
  height: 24px;
  width: 24px;
  line-height: 24px;
  border: 1px solid black;
  border-radius: 50%;
  margin: 23px 15px 0 0;
  cursor: pointer;
  font-weight: bold;
}

.users button.delete:hover {
  background-color: darkred;
  color: #eee;
  border: 2px solid #eee;
}
.container input {
  margin-right: 5px;
  display: block;
  margin: 5px auto;
  padding: 10px 0 10px 5px;
  border: 1px solid #333;
  font-size: 14px;
}

.container form label {
  display: inline-block;
  font-size: 14px;
  margin-bottom: 10px;
}

.container .addUser input:nth-of-type(1),
.container .addUser input:nth-of-type(2) {
  display: inline-block;
  margin: 5px 5px;
}

.container h2 {
  text-transform: uppercase;
  color: #333;
}

.container {
  display: -webkit-flex;
  display: flex;
  padding: 0 10%;
}

.container .addUser {
  margin: 0;
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
  border-right: 2px solid #333;
}

.container .updateUser {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.container span {
  font-weight: bold;
  /* text-decoration: underline; */
  border-bottom: 2px solid #eee;
}

.container .updateUser {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
}

.container .updateUser input:nth-of-type(1) {
  margin: 20px auto;
}

form .btn {
  background-color: #eee;
  background-color: #337ab7;
  border: 1px solid #eee;
  padding: 10px 20px;
  margin: 10px auto;
  text-transform: uppercase;
  color: #eee;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  letter-spacing: 1px;
}

form .btn:hover {
  color: white;
  background-color: #245782;
  /* border: 2px solid white; */
}
.app {
  text-align: center;
  background-image: url(/rest-api/static/media/bgc.c0c832a9.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  max-width: 1920px;
  margin: 0 auto;
}

.app h1 {
  text-transform: uppercase;
  margin: 0;
  color: #eee;
  background-color: rgba(0, 0, 0, 0.3);
  letter-spacing: 2px;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 50px;
  font-weight: bold;
  padding: 4vh 0;
  text-shadow: 2px 2px 2px black, 2px 2px 5px darkgrey;
}

footer {
  width: 100%;
  background-color: #141414;
  color: #e5e5e5;
  letter-spacing: 2px;
  padding: 5px 0;
  font-size: 20px;
}
